<template>
    <!-- <li class="border border-slate-600 rounded-md text-sm font-inter-normal p-3 select-none duration-300" -->
        <!-- 'text-slate-600 cursor-pointer hover:scale-105': notOneButtonSelected, -->
    <li class="answer-button"
    :class="{
          'answer-button-selected': isSelected,
          'answer-button-not-selected': !isSelected && !isDisable,

        }"
    @click="buttonClick"
    >
        <!-- <span>{{answerButtonInfo.buttonText}}</span> -->
        
        <div class="answer-button-text">
            {{answerButtonInfo.buttonText}}
        </div>

    </li>
</template>

<script>
export default {
    name: "AnswerButton",
    components: {

    },
    props: {
        answerButtonInfo: {},
        answerNumber: Number,
        currentAnswerNumber: Number,
        notOneButtonSelected: Boolean,
        isDisable: Boolean
    },
    computed: {
        isSelected() {            
            if (this.answerNumber === this.currentAnswerNumber) {
                return true
            }
            else {
                return false
            }
        }
    },
    methods: {
        buttonClick(){
            if (!this.isDisable) {
                this.$emit("selectButton", this.answerNumber)
            }
        }
    },
    mounted() {
        
    }

}
</script>

<style>
    .answer-button{
        min-height: 78px;
        border: 1px solid #787D7F;
        display: flex;
        align-items: center;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 19px;
        line-height: 23px;
        color: #121717;
        transition-duration: 300ms;
        user-select: none;
    }
    .answer-button-selected{
        background: #5735AC;
        border: 2px solid #5735AC;
        color: #FFF9F2;
    }
    .answer-button-not-selected{
        /* color: #121717; */
        cursor: pointer;
    }
    .answer-button-not-selected:hover{
        scale: 105%;
    }
    .answer-button-text{
        padding-left: 35px;
        padding-right: 35px;
        /* font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 19px;
        line-height: 23px;
        color: #121717; */
    }
</style>