import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    testId: 0,
    testData: {},
    gameData: [],
    actualView: {},
    actualProperty: {},
    stepNumber: 0,
    propertyList: [],
    feedback: {    
      isSent: false,
      useful: true,      
      complexity: 0,
      frequency: 0,
      disagreeDiagnosis: false,
      stars: 0,
      comment: '',
      interestingCase: ''
    }
  },
  getters: {
    gameData(state){
      return state.gameData
    },
    stepNumber(state){
      return state.stepNumber
    },
    actualView(state){
      return state.actualView
    },
    actualProperty(state){
      return state.actualProperty
    },
    completed(state){
      return state.actualProperty.completed
    },
    feedback(state){
      return state.feedback
    }    
  },
  mutations: {
    SET_TESTDATA_TO_STATE(state, testData) {
      state.testData = testData
      state.testId = testData.data.data.id
    },
    SET_GAMEDATA_TO_STATE(state, gameData) {
      state.gameData = gameData
    },
    setStepNumber(state, stepNumber){
      state.stepNumber = stepNumber
    },
    setActualView(state, actualView){
      state.actualView = actualView
    },
    createEmptyPropertyList(state, gameDataQuestions) {
      const propertyList = store.state.propertyList
      if (propertyList.length > 0) {
        return        
      }
      const gameData = gameDataQuestions
      
      for (let i = 0; i < gameData.length; i++) {
        let currentProperty = {}
        
        currentProperty.completed = false
        if (gameData[i].body.type === "sections") {
          currentProperty.viewedSections = []
          for (let j = 0; j < gameData[i].body.sections.length; j++) {
            currentProperty.viewedSections.push(false)
          }
        }
        else if (gameData[i].body.type === "answersButtons") {
          currentProperty.currentAnswer = null
          currentProperty.numberTry = 0
        }
        else if (gameData[i].body.type === "answersCheckboxes") {
          currentProperty.checkboxList = []
          for (let j = 0; j < gameData[i].body.bodyCheckboxes.length; j++) {
            currentProperty.checkboxList.push(false)
          }
          currentProperty.numberTry = 0
        }
        if (gameData[i].body.acceptCheckbox) {
          currentProperty.acceptCheckbox = false
        }
        
        propertyList.push(currentProperty)
        
      }
      state.propertyList = propertyList
    },
    setActualProperty(state, stepNumber){
      state.actualProperty = state.propertyList[stepNumber]
    },

    setCurrentAnswer(state, answerNumber){
      state.actualProperty.currentAnswer = answerNumber
      // const oldObj = state.currentAnswersState[state.stepNumber]
      // const newObj = {...oldObj, currentAnswer: answerNumber,}
      // Vue.set(state.currentAnswersState, state.stepNumber, newObj)
    },
    changeAnswerCheckbox(state, checkboxNumber){      
      // state.actualProperty.checkboxList[checkboxNumber] = !state.actualProperty.checkboxList[checkboxNumber]
      Vue.set(state.actualProperty.checkboxList, checkboxNumber, !state.actualProperty.checkboxList[checkboxNumber])
    },
    setCompleted(state, completed){
      state.actualProperty.completed = completed
    },
    setFeedbackItem(state, feedbackItem){
      state.feedback[feedbackItem.name] = feedbackItem.value
    },
    setAcceptCheckbox(state, acceptCheckbox){
      state.actualProperty.acceptCheckbox = acceptCheckbox
    },
    setViewedSection(state, numberSection){
      state.actualProperty.viewedSections[numberSection] = true
    },
    clearState(state){
      state.propertyList.length = 0
      state.feedback = {    
        isSent: false,
        useful: true,      
        complexity: 0,
        frequency: 0,
        disagreeDiagnosis: false,
        stars: 0,
        comment: '',
        interestingCase: ''
      }
    }

  },
  actions: {
    GET_DATA_FROM_API({commit},testNumber){
      // return axios(`http://188.225.46.190/api/v1/test/${testNumber}`, {
      return axios(`https://tech1.med-game.ru/api/v1/test/${testNumber}`, {
        method: "GET"
      })
        .then((testData) => {
          commit('SET_TESTDATA_TO_STATE', testData)
          return testData.data.data.questions
        })
        .then((gameDataQuestions) => {
          commit('SET_GAMEDATA_TO_STATE', gameDataQuestions)
          return gameDataQuestions
        })
        .then((gameDataQuestions) => {
          commit('createEmptyPropertyList', gameDataQuestions)
          return gameDataQuestions
        })
        .then((gameDataQuestions) => {
          commit('setStepNumber', 0)
          return gameDataQuestions[0].body
        })
        .then((gameDataQuestionsActual) => {
          commit('setActualView', gameDataQuestionsActual)
          return 0
        })
        .then((stepNumber) => {
          commit('setActualProperty', stepNumber)
          return
        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
    },
    GET_DATA_FROM_LOCAL({commit}, testData){
      commit('SET_TESTDATA_TO_STATE', testData)
      commit('SET_GAMEDATA_TO_STATE', testData.data.data.questions)
      commit('createEmptyPropertyList', testData.data.data.questions)
      commit('setStepNumber', 0)
      return store.dispatch('SET_ACTUAL_VIEW', store.state.stepNumber)
    },
    SET_ACTUAL_VIEW({commit},stepNumber){
      commit('setActualView', store.state.gameData[stepNumber].body)
      return store.dispatch('SET_ACTUAL_PROPERTY', stepNumber)
    },
    SET_ACTUAL_PROPERTY({commit},stepNumber){
      commit('setActualProperty', stepNumber)
    },

    // GET_DATA_FROM_GOOGLE(){
    //   return axios('https://jsonplaceholder.typicode.com/posts/1', {
    //     method: "GET"
    //   })
    //     .then((testData) => {
    //       console.log(testData)
    //       // console.log(commit)
    //       // commit('viewLog'.testData)
    //       return testData
    //     })
    //     .catch((error) => {
    //       console.log('Ошибка:');
    //       console.log(error)
    //       return error
    //     })
    // },
    SEND_DATA_TO_API(store, dataQuestion){
      // axios.post('http://188.225.46.190/api/v1/admin/answers', {
      axios.post('https://tech1.med-game.ru/api/v1/admin/answers', {
        "question_id": store.state.gameData[store.state.stepNumber].id,
        "test_id": store.state.testId,
        "body": {
          "description": dataQuestion
        }
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);        
      });
    },
    setStepNumber(store, stepNumber){
      if (store.state.stepNumber !== stepNumber) {
        store.commit('setStepNumber', stepNumber)
        store.commit('setActualProperty', stepNumber)
        return store.dispatch('setActualView', store.state.gameData[stepNumber].body)
      }
    },
    setActualView(store, actualView){
      store.commit('setActualView', actualView)
      // return store.dispatch('setActualProperty', store.state.propertyList[store.state.stepNumber])
    },
    setActualProperty(store, actualProperty){
      store.commit('setActualProperty', actualProperty)
    },    
    nextStep(store){
      if (store.state.stepNumber+1 < store.state.gameData.length) {
        return store.dispatch('setStepNumber', store.state.stepNumber+1)
      }
    },
    previousStep(store){
      if (store.state.stepNumber > 0) {
        return store.dispatch('setStepNumber', store.state.stepNumber-1)
      }
    },
    returnActualView(store){
      return store.dispatch('setActualView', store.state.gameData[store.state.stepNumber].body)
    },
    setCurrentAnswer(store, answerNumber){
      store.commit('setCurrentAnswer', answerNumber)
    },    
    changeAnswerCheckbox(store, checkboxNumber){
      store.commit('changeAnswerCheckbox', checkboxNumber)
    },
    setCompleted(store, completed){
      store.commit('setCompleted', completed)
    },
    setFeedbackItem(store, feedbackItem){
      store.commit('setFeedbackItem', feedbackItem)
    },
    setAcceptCheckbox(store, acceptCheckbox){
      store.commit('setAcceptCheckbox', acceptCheckbox)
    },
    setViewedSection(store, numberSection){
      store.commit('setViewedSection', numberSection)
    },
    newStart(store){      
      store.commit('clearState')
      return store.dispatch('GET_DATA_FROM_API', store.state.testId)
      // return store.dispatch('GET_DATA_FROM_LOCAL', store.state.testData)
    }
    
  },
  modules: {
  }
})
