<template>
    <!-- <div class="flex gap-4"> -->
    <div class="footer-buttons">
        <FooterButton
        v-for="(item, index) in actualView.footerButtonList"
        :key="index"
        :buttonData = item
        @buttonClick = buttonClick($event)
        />

        <ModalWarning
        v-if="showModal"
        :correct = "correct"
        :warningText = "warningText"
        @closeModal = "closeModal"
        />

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import FooterButton from "@/components/game/FooterButton.vue"
import ModalWarning from "@/components/game/ModalWarning.vue"

export default {
    name: 'FooterButtons',
    components: {
        FooterButton,
        ModalWarning
    },
    data() {
        return {
            showModal: false,
            correct: false,
            warningText: ""
        }
    },
    computed: {
        ...mapGetters([
            "actualView",
            "actualProperty",
            "completed",
            "stepNumber",
            "testId"
        ])        
    },
    methods: {
        ...mapActions([
            "nextStep",
            "previousStep",
            "returnActualView",
            "setCompleted",
            "setCurrentAnswer",
            "SEND_DATA_TO_API",
            "newStart"
        ]),
        buttonClick(action){
            if (action === 'next') {
                if (this.completed) {
                    this.nextStep()
                }
                else {
                    if (this.actualView.type === "answersButtons") {                    
                        if (this.actualProperty.currentAnswer && this.actualView.bodyButtons[this.actualProperty.currentAnswer].isRight) {
                            
                            this.SEND_DATA_TO_API(
                                {
                                    "type": this.actualView.type,
                                    "currentAnswer": this.actualProperty.currentAnswer,
                                    "isRight": true,
                                    "numberTry": this.actualProperty.numberTry
                                }
                            )
                            
                            this.setCompleted(true)
                            this.openModal(true, "Диагноз верный!", () =>{this.nextStep()})
                        }
                        else {                        
                            if (this.actualProperty.numberTry === 2) {
                                this.actualProperty.numberTry += 1
                                this.SEND_DATA_TO_API(
                                    {
                                        "type": this.actualView.type,
                                        "currentAnswer": this.actualProperty.currentAnswer,
                                        "isRight": false,
                                        "numberTry": this.actualProperty.numberTry
                                    }
                                )
                                this.setCompleted(true)
                                this.setTrueAnswer()                            
                            }
                            else if (this.actualProperty.numberTry < 2){
                                this.SEND_DATA_TO_API(
                                    {
                                        "type": this.actualView.type,
                                        "currentAnswer": this.actualProperty.currentAnswer,
                                        "isRight": false,
                                        "numberTry": this.actualProperty.numberTry
                                    }
                                )
                                this.actualProperty.numberTry += 1
                                this.openModal(false, "Диагноз не верный!", ()=>{})                                
                            }
                        }
                    }
                    else if (this.actualView.type === "answersCheckboxes") {
                        for (let i = 0; i < this.actualView.bodyCheckboxes.length; i++) {
                            if (this.actualView.bodyCheckboxes[i].isRight !== this.actualProperty.checkboxList[i]) {                            
                                if (this.actualProperty.numberTry === 2) {
                                    this.actualProperty.numberTry += 1
                                    this.SEND_DATA_TO_API(
                                    {
                                        "type": this.actualView.type,
                                        "currentAnswer": this.actualProperty.checkboxList,
                                        "isRight": false,
                                        "numberTry": this.actualProperty.numberTry
                                    }
                                    )
                                    this.setTrueCheckboxes(() => {this.setCompleted(true)})
                                    return
                                }
                                else if (this.actualProperty.numberTry < 2){
                                    this.actualProperty.numberTry += 1

                                    this.SEND_DATA_TO_API(
                                    {
                                        "type": this.actualView.type,
                                        "currentAnswer": this.actualProperty.checkboxList,
                                        "isRight": false,
                                        "numberTry": this.actualProperty.numberTry
                                    }
                                    )

                                    this.openModal(false, "Скорректируйте лечение!", ()=>{})
                                    return
                                }                                
                            }
                        }
                        
                        this.SEND_DATA_TO_API(
                            {
                                "type": this.actualView.type,
                                "currentAnswer": this.actualProperty.checkboxList,
                                "isRight": true,
                                "numberTry": this.actualProperty.numberTry
                            }
                        )
                        
                        this.setCompleted(true)
                        this.openModal(true, "Лечение верное!", () =>{this.nextStep()})
                    }
                    else if (this.actualView.type === "sections"){

                        // for (let i = 0; i < this.actualProperty.viewedSections.length; i++) {
                        //     if (!this.actualProperty.viewedSections[i]) {
                        //         this.openModal(false, "Осмотр проведен не полностью", ()=>{})
                        //         return
                        //     }
                        // }
                        this.SEND_DATA_TO_API(
                            {
                                "type": this.actualView.type
                            }
                        )

                        this.setCompleted(true)
                        this.nextStep()
                    }
                    else if (this.actualView.type === "info"){
                        if ((this.actualView.acceptCheckbox) && (this.actualProperty.acceptCheckbox === false)) {
                            return
                        }
                        this.SEND_DATA_TO_API(
                            {
                                "type": this.actualView.type
                            }
                        )

                        this.setCompleted(true)
                        this.nextStep()
                    }
                }
            }
            if (action === 'previous') {
                this.previousStep()
            }
            if (action === 'return') {
                this.returnActualView()
            }
            if (action === 'startAgain') {
                this.newStart()
            }
        },
        
        async openModal(correct, warningText, callback){
            this.correct = correct
            this.showModal = true
            this.warningText = warningText
            await this.sleep(1000)
            this.showModal = false
            callback()
        },
        closeModal(){
            this.showModal = false
        },

        setTrueAnswer(){
            for (let i = 0; i < this.actualView.bodyButtons.length; i++) {
                if (this.actualView.bodyButtons[i].isRight) {
                    this.setCurrentAnswer(i)
                }
            }
        },
        async setTrueCheckboxes(callback){
            for (let i = 0; i < this.actualView.bodyCheckboxes.length; i++) {
                this.actualProperty.checkboxList[i] = this.actualView.bodyCheckboxes[i].isRight
            }
            // await this.sleep(1000)
            callback()
        },

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        }       
        
    },
}
</script>

<style>
    .footer-buttons{
        display: flex;
        padding-bottom: 60px;
        padding-top: 30px;
        column-gap: 40px;
    }
</style>