<template>
    <div
    @click="openSection()"
    class="section-item"
    >
        <!-- class=" hover:scale-105 duration-300 w-full min-h-[140px] h-full flex flex-col items-center justify-between shadow-md border-2 border-slate-700 rounded-md" -->
        <!-- <button 
        @click="openSection()"
        class="section-button">
            <img :src="buttonImage" class="p-2 max-w-[80px]" alt="button image">
            <div class="h-[50px]">
                <span>{{buttonName}}</span>
            </div>
        </button> -->

        <div class="section-image-container">
            <img :src="buttonImage" alt="button image">
        </div>
        <div class="section-text">
            {{buttonName}}
        </div>

    </div>
</template>

<script>

export default {
    name: "SectionButton",    
    props: {
        sectionItem: {},
        numberItem: Number
    },
    computed: {
        buttonImage() {
            return require(`@/assets/images/${this.sectionItem.image}`)
        },
        buttonName() {
            return this.sectionItem.sectionName
        }
    },
    methods: {
        openSection(){
            this.$emit('openSection', this.numberItem)
        }
    }
}
</script>

<style>
    .section-item{
        background: #F4F5F7;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
        width: 260px;
        height: 260px;
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;
        transition-duration: 300ms;
    }
    .section-item:hover{
        scale: 105%;
    }
    /* .section-button{
        cursor: pointer;
        transition-duration: 300ms;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        width: 100%;
        min-height: 140px;
        height: 100%;
    } */
    .section-image-container{
        max-width: 144px;
        max-height: 144px;
        margin: 20px;
    }
    .section-text{
        margin-left: 20px;
        margin-right: 20px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        color: #4D4D4D;
    }
</style>