<template>
    <!-- class="fixed w-full h-screen left-0 top-0 flex justify-center items-center" -->
    <!-- class="bg-green-100 px-12 py-6 border-2 border-green-400 rounded-md z-20 font-inter-normal text-md text-green-400" -->
    <!-- class="bg-rose-100 px-12 py-6 border-2 border-rose-400 rounded-md z-20 font-inter-normal text-md text-rose-400" -->
    <div
      class="modal-warning"
      @click="closeModal"
    >    
        <div
            v-if="correct"
            class="correct-warning"
        >
            <div class="warning-text">
                {{warningText}}
            </div>
            
        </div>
        <div
            v-else
            class="uncorrect-warning"
        >
            <div class="warning-text">
                {{warningText}}
            </div>
        </div>
    </div>      
</template>

<script>
export default {
    name: 'ModalWarning',
    props: {
        correct: Boolean,
        warningText: String
    },
    methods: {
        closeModal() {
            this.$emit("closeModal")
        }
    }
}
</script>

<style>
    .modal-warning{
        position: fixed;
        width: 100%;
        height: 100vh;
        left: 0px;
        top: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .correct-warning{
        width: 503px;
        height: 177px;
        background: #A3D65C;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .warning-text{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF;
    }
    .uncorrect-warning{
        width: 503px;
        height: 177px;
        background: #FF6D6D;
        display: flex;
        justify-content: center;
        align-items: center;
    }

</style>