<template>
    <div class="answers-buttons">   
        <AnswerHint
        :numberTry = actualProperty.numberTry
        />
        <!-- <ul class="grid grid-cols-1 gap-5 sm:grid-cols-2"> -->
        <ul class="answers-buttons-grid">
            <AnswerButton
            v-for="(item,index) in actualView.bodyButtons"
            :key="index"
            :answerButtonInfo = item
            :answerNumber = index
            :currentAnswerNumber = currentAnswerNumber
            :notOneButtonSelected = notOneButtonSelected
            :isDisable = "actualProperty.completed"
            @selectButton = "selectButton($event)"
            />
        </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
// import GameFooter from '@/components/game/GameFooter.vue'
// import GameTitle from '@/components/game/GameTitle.vue'
import AnswerButton from '@/components/game/AnswerButton.vue'
import AnswerHint from '@/components/game/AnswerHint.vue'

export default {
    name: "AnswersButtons",
    components: {
        // GameFooter,
        // GameTitle,
        AnswerButton,
        AnswerHint
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ]),
      notOneButtonSelected() {
            if (this.actualProperty.currentAnswer === null) {
                return true
            }
            else {
                return false
            }
        },

        currentAnswerNumber() {
            return this.actualProperty.currentAnswer
        }
      
    },
    methods: {
        ...mapActions([
        "setCurrentAnswer"
        ]),
        selectButton(answerNumber){            
            this.setCurrentAnswer(answerNumber)
        }        
    },
}
</script>

<style>
    .answers-buttons{
        padding-top: 20px;
        padding-left: 80px;
        padding-right: 80px;
    }
    .answers-buttons-grid{
        padding-top: 45px;
        padding-bottom: 30px;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        row-gap: 20px;
        column-gap: 40px;
    }
</style>