<template>
    <div 
    :class="{'colored-indicator': isColored, 'not-colored-indicator': !isColored}"
    class="indicator-item"
    >
    
    </div>
</template>

<script>
export default {
    name: 'IndicatorItem',
    props:{
        indicatorNumber: Number,
        stepNumber: Number
    },
    computed: {
        isColored(){
            if (this.indicatorNumber <= this.stepNumber) {
                return true
            }
            else {
                return false
            }
        }
    }
}
</script>

<style>
    .colored-indicator{
        background-color: #D16BBE;
    }
    .not-colored-indicator{
        background-color: #D9D9D9;
    }
    .indicator-item{
        height: 16px;
        width: 100%;
        margin: 4px 4px;
    }
</style>
