<template>
    <div>
        <!-- <GameTitle 
        :titleText = actualView.title
        /> -->

        <!-- <div
        class="px-8 py-6 grid grid-cols-2 sm:grid-cols-4 font-inter-semi-bold text-md text-slate-600 justify-between"
        > -->
        <div class="sections-wrapper">
        <SectionButton
        v-for="(item,index) in actualView.sections"
        :key="index"
        :sectionItem = item
        :numberItem = index
        @openSection = openSection($event)
        />

        </div>

        <!-- <GameFooter 
            :footerData ="actualView.footer"
        /> -->
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
// import GameFooter from '@/components/game/GameFooter.vue'
// import GameTitle from '@/components/game/GameTitle.vue'
import SectionButton from '@/components/game/SectionButton.vue'

export default {
    name: "GameSections",
    components: {
        // GameFooter,
        // GameTitle,
        SectionButton
    },
    computed: {
      ...mapGetters([
          "actualView"
      ])       
    },
    methods: {
        ...mapActions([
          "setActualView",
          "setViewedSection"
        ]),
        openSection(numberSection){
            this.setActualView(this.actualView.sections[numberSection].sectionContent)
            this.setViewedSection(numberSection)
        }
    },
}
</script>

<style>
    .sections-wrapper{
        padding-top: 50px;
        padding-bottom: 65px;
        display: flex;
        column-gap: 40px;
        justify-content: center;
    }
</style>