<template>
    <div class="answer-hint">
        После трех неверных ответов будет дана подсказка. Осталось {{remainedTry}} попытки из 3-х.
    </div>
</template>

<script>
export default {
    name: 'AnswerHint',
    props: {
      numberTry: Number
    },
    computed:{
        remainedTry(){
            return 3 - this.numberTry
        }
    }
}
</script>

<style>
    .answer-hint{
        /* padding-top: 20px; */
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
        color: #5735AC;
    }
</style>