<template>
    <div>
        <div class="answers-checkboxes">
            <h2 class="answers-checkboxes-title">
                {{actualView.headerText}}
            </h2>

            <AnswerHint
            :numberTry = actualProperty.numberTry
            />

            <!-- <ul class="grid grid-cols-1 gap-4 sm:grid-cols-2"></ul> -->
            
            <ul class="answers-checkboxes-grid">
                <AnswerCheckbox 
                v-for="(item,index) in actualView.bodyCheckboxes"
                :key="index"
                :answerCheckboxInfo = item
                :isChecked = actualProperty.checkboxList[index]
                :answerNumber = index
                :isDisable = actualProperty.completed
                @selectCheckbox = selectCheckbox(index)
                />
            </ul>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AnswerCheckbox from '@/components/game/AnswerCheckbox.vue'
import AnswerHint from '@/components/game/AnswerHint.vue'

export default {
    name: 'AnswersCheckboxes',
    components: {
        AnswerCheckbox,
        AnswerHint
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ])      
    },
    methods: {
        ...mapActions([
        "changeAnswerCheckbox"
        ]),
        selectCheckbox(index){
            this.changeAnswerCheckbox(index)     
        }
    }
}
</script>

<style>
    .answers-checkboxes{
        padding-top: 8px;
        padding-left: 112px;
        padding-right: 112px;
        padding-bottom: 40px;
    }
    .answers-checkboxes-title{
        padding-bottom: 25px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
        color: #121717;
    }
    .answers-checkboxes-grid{
        padding-top: 50px;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        column-gap: 40px;
        row-gap: 30px;
    }
</style>