<template>
    <!-- <li 
    class="text-sm font-inter-normal select-none flex text-slate-600 w-full justify-start"    
    > -->
    <!-- <input
          type="checkbox"
          class="w-5 h-5 mr-4 shrink-0"
          :id = "checkboxId"
          :checked = "isChecked"
          :disabled="isDisable"
          @click = "selectCheckbox"
        >
        <label :for="checkboxId" class="w-full">
            {{answerCheckboxInfo.checkboxText}}
        </label> -->

    <li>
        <div class="answer-checkbox">
            <div>
                <div 
                @click="selectCheckbox"
                :class="{'white-checkbox': !isChecked,
                        'purple-checkbox': isChecked,
                        'cursor-pointer': !isDisable
                }"
                class="answer-checkbox-rectangle">
                    <img 
                    v-if="isChecked"
                    src="@/assets/images/answerCheckMark.svg" alt="check mark">
                </div>
            </div>

            <div
            @click="selectCheckbox"
            :class="{'cursor-pointer': !isDisable}"
            class="answer-checkbox-text">
                {{answerCheckboxInfo.checkboxText}}
            </div>
      </div>

    </li>
</template>

<script>
export default {
    name: 'AnswerCheckbox',
    props: {
        answerCheckboxInfo: {},
        answerNumber: Number,
        isChecked: Boolean,
        isDisable: Boolean
    },
    computed: {
        // _isChecked: {
        //     get() {
        //         return this.isChecked
        //     },
        //     set() {
        //         this.selectCheckbox()
        //     }
        // },

        // checkboxId() {
        //     return ("checkbox" + this.answerNumber)            
        // }
    },
    methods: {
        selectCheckbox() {
            if (!this.isDisable) {
                this.$emit('selectCheckbox', this.answerNumber)
            }
        }        
    }
}

</script>

<style>
    .answer-checkbox{
        display: flex;
    }
    .answer-checkbox-rectangle{
        width: 25px;
        height: 25px;        
        border: 1px solid #121717;
        border-radius: 3px;
        position: relative;
        
    }
    .cursor-pointer{
        cursor: pointer;
    }
    .white-checkbox{
        background: #FFFFFF;
    }
    .purple-checkbox{
        background: #5735AC;
    }
    .answer-check-mark{
        position: absolute;
    }
    .answer-checkbox-text{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        color: #121717;
        padding-left: 25px;
        user-select: none;
    }
</style>