<template>
    <div class="notification-wrapper">
        <div class="notification-text">
            Материал создан исключительно в образовательных целях и предназначен только для специалистов здравоохранения. Любое совпадение с реальными пациентами случайно. Тактика лечения определяется лечащим врачом.
        </div>

        <AcceptCheckbox
        v-if="actualView.acceptCheckbox"  
        :isAccept=isAccept
        @changeAccept = changeAccept
        />

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AcceptCheckbox from '@/components/game/AcceptCheckbox.vue'

export default {
    name: 'InfoNotification',
    data() {
      return {
        isAccept: false
      }
    },
    components: {
        AcceptCheckbox
    },
    computed: {
      ...mapGetters([
          "actualView"
      ]),
    },
    methods: {
      ...mapActions([
          "setAcceptCheckbox"
      ]),
      changeAccept() {
        this.isAccept = !this.isAccept
        this.setAcceptCheckbox(this.isAccept)
      }
    }
}
</script>

<style>
    .notification-wrapper{
        padding-top: 27px;
        padding-left: 170px;
        padding-right: 170px;
        border-top: 1px solid #D16BBE;
        display: flex;
        flex-direction: column;

    }
    .notification-text{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #121717;
    }
</style>
