<template>
    <div class="percent-wrapper">
      <img src="@/assets/images/cup.svg" alt="cup" width="30" class="percent-image"/>
      <span class="percent-text">
        Только {{randomPercent}}% пользователей сумели пройти этот сценарий с первого
        раза!
      </span>      
    </div>
</template>

<script>
export default {
  name: 'InfoPercent',
  computed: {
    randomPercent(){
        return (Math.random() * (73 - 61) + 61).toFixed(0)
    }    
  }
}
</script>

<style>
  .percent-wrapper{
    display: flex;
    padding-top: 15px;
    padding-bottom: 15px;
    width: 100%;
  }
  .percent-image{
    margin-right: 90px;
    margin-left: 5px;
  }
  .percent-text{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #D16BBE;
  }
</style>